import { io } from 'socket.io-client';
// Instance socket pour le chat
let chatSocket = null;
// Vrifie la connexion utilisateur via Socket.IO
export async function isUserLoggedIn() {
    return new Promise((resolve) => {
        console.log(' Vrification auth via socket...');
        // Si pas de socket, crer une connexion
        if (!chatSocket) {
            const ip = window.location.hostname;
            const protocol = window.location.protocol === 'https:' ? 'https' : 'http';
            const socketUrl = `${protocol}://${ip}:8080/chat`;
            console.log(' Cration nouvelle socket:', socketUrl);
            chatSocket = io(socketUrl, {
                withCredentials: true,
                autoConnect: true,
                timeout: 10000, // 10 secondes de timeout
                transports: ['websocket', 'polling'] // Fallback sur polling si websocket choue
            });
            // couter les vnements de connexion
            chatSocket.on('connect', () => {
                console.log(' Socket connecte:', chatSocket?.id);
            });
            chatSocket.on('connect_error', (error) => {
                console.log(' Erreur connexion socket:', error);
                resolve(false);
            });
            chatSocket.on('disconnect', (reason) => {
                console.log(' Socket dconnecte:', reason);
            });
        }
        // Attendre que la socket soit connecte avant de vrifier l'auth
        const checkAuth = () => {
            if (chatSocket?.connected) {
                console.log(' Envoi check-auth...');
                chatSocket.emit('check-auth', (response) => {
                    console.log(' Rponse auth socket:', response);
                    resolve(response?.authenticated === true);
                });
            }
            else {
                console.log(' Socket pas encore connecte, retry dans 100ms...');
                setTimeout(checkAuth, 100);
            }
        };
        // Commencer la vrification
        setTimeout(checkAuth, 100); // Petit dlai pour laisser la socket se connecter
    });
}
// Initialise le chat si l'utilisateur est connect
export async function initChatIfLoggedIn() {
    console.log(' initChatIfLoggedIn() appele');
    if (await isUserLoggedIn()) {
        console.log(' Utilisateur connect - Affichage du chat');
        await displayChatButton();
    }
    else {
        console.log(' Utilisateur non connect - Masquage du chat');
        hideChatButton();
    }
}
// Afficher le bouton de chat aprs connexion
export async function displayChatButton() {
    if (document.getElementById('chat-fab')) {
        console.log('Bouton chat dj prsent');
        return;
    }
    console.log(' Cration du bouton chat aprs connexion');
    const { ChatFloatingButton } = await import('../views/chat.views');
    document.body.insertAdjacentHTML('beforeend', ChatFloatingButton());
    const { chatManager } = await import('../chat/chat');
}
// Masquer le bouton de chat aprs dconnexion
export function hideChatButton() {
    const chatButton = document.getElementById('chat-fab');
    const chatPanel = document.getElementById('chat-panel');
    if (chatButton) {
        chatButton.remove();
        console.log(' Bouton chat supprim aprs dconnexion');
    }
    if (chatPanel) {
        chatPanel.remove();
    }
    // Nettoyer la socket lors de la dconnexion
    if (chatSocket) {
        chatSocket.disconnect();
        chatSocket = null;
        console.log(' Socket chat ferme');
    }
}
