export function initThemeToggle() {
    const savedTheme = localStorage.getItem('theme') || 'light'; //recup le theme sauvegarde ou utiliser light par defaut
    applyTheme(savedTheme);
    //ajout de l'event au bouton
    const themeToggle = document.getElementById('theme-toggle');
    if (themeToggle) {
        themeToggle.addEventListener('click', toggleTheme);
    }
}
function toggleTheme() {
    const currentTheme = document.documentElement.classList.contains('dark') ? 'dark' : 'light';
    const newTheme = currentTheme == 'light' ? 'dark' : 'light';
    applyTheme(newTheme);
    localStorage.setItem('theme', newTheme);
}
function applyTheme(theme) {
    const html = document.documentElement;
    const themeText = document.getElementById('theme-text');
    const themeIcon = document.getElementById('theme-icon');
    const mainTitle = document.getElementById('main-title');
    if (theme == 'dark') {
        html.classList.add('dark');
        if (themeText)
            themeText.textContent = 'Dark mode';
        if (themeIcon)
            themeIcon.textContent = '';
        if (mainTitle) {
            mainTitle.classList.remove('text-black');
            mainTitle.classList.add('text-white');
        }
    }
    else {
        html.classList.remove('dark');
        if (themeText)
            themeText.textContent = 'Light mode';
        if (themeIcon)
            themeIcon.textContent = '';
        if (mainTitle) {
            mainTitle.classList.remove('text-white');
            mainTitle.classList.add('text-black');
        }
    }
}
// Fonction pour nettoyer les event listeners
export function cleanupThemeToggle() {
    const themeToggle = document.getElementById('theme-toggle');
    if (themeToggle) {
        themeToggle.removeEventListener('click', toggleTheme);
    }
}
